import {webServer} from '../../config.js'
let lotusAddressJson
uni.request({
	url: `${webServer}xcx.web/Resources/Js/XCX/lotusAddress.js`,
	method:'GET',
	success: (res) => {
		lotusAddressJson =res.data 
	}
})
export default {
	props:['lotusAddressData'],
	data() {
		return {
			visible: false,
			province:[],
			city:[],
			town:[],
			provinceName:'',
			cityName:'',
			townName:'',
			type:0,//0新增1编辑
			pChoseIndex:-1,
			cChoseIndex:-1,
			tChoseIndex:-1
		};
	},
	methods:{
		//取消
		cancelPicker(){
			this.$parent.lotusAddressData.visible = false   //暂定
			this.visible = false
		},
		//获取最后选择的省市区的值
		chosedVal() {
			this.type = 1;
			let districtNo=null;
			
			const provinceCode = this.getTarId(this.provinceName);
			const cityCode = this.getTarId(this.cityName);
			const townCode = this.getTarId(this.townName);
			if(townCode){
				districtNo=townCode
			}
			else if(cityCode){
				districtNo=cityCode
			}
			else if(provinceCode){
				districtNo=provinceCode
			}
			this.visible = false;
			let isChose = 0;
			if((this.provinceName&&this.cityName)||(this.provinceName&&this.cityName&&this.townName)){
				isChose = 1;
			}
			this.$emit("choseVal",{
				province:this.provinceName,
				provinceCode,
				city:this.cityName,
				cityCode,
				town:this.townName,
				townCode,
				isChose,
				visible:false,
				districtNo:districtNo
			});
		},
		//获取省市区value
		getTarId(name,type){
		    let id = 0;
		    lotusAddressJson.map((item,index)=>{
		        if(item.name === name){
		            id = item.value;
		        }
		    });
		    return id;
		},
		//获取市数据
		getCityArr(parentId){
		    let city = [];
		    lotusAddressJson.map((item,index)=>{
		        if(item.parent === parentId){
		            city.push(item.name);
		        }
		    });
		    return city;
		},
		//获取区数据
		getTownArr(parentId){
		    let town = [];
		    lotusAddressJson.map((item,index)=>{
		        if(index>34&&item.parent === parentId){
		            town.push(item.name);
		        }
		    });
		    return town;
		},
		//初始化数据
		initFn(){
			if(!this.province.length){
				lotusAddressJson.map((item,index)=>{
					if(index<=34){
						this.province.push(item.name);
					}
				});
			}
			//已选择省市区，高亮显示对应选择省市区
			const p = this._props.lotusAddressData.provinceName;
			const c = this._props.lotusAddressData.cityName;
			const t = this._props.lotusAddressData.townName;
			//已选省
			if(p){
				this.pChoseIndex = this.getTarIndex(this.province,p);
			}
			//已选市
			if(p&&c){
				const pid = this.getTarId(p);
				this.city = this.getCityArr(pid);
				this.cChoseIndex = this.getTarIndex(this.city,c);
			}
			//已选区
			if(p&&c&&t){
				const cid= this.getTarId(c);
				this.town = this.getTownArr(cid);
				this.tChoseIndex = this.getTarIndex(this.town,t);
			}
			//未选省市区
			if(!p&&!c&&!t){
				this.pChoseIndex = -1;
				this.cChoseIndex = -1;
				this.tChoseIndex = -1;
				this.city = [];
				this.town = [];
			}
		},
		//获取已选省市区
		getChosedData(){
			const pid = this.getTarId(this.provinceName,'province');
			this.city = this.getCityArr(pid);
			const cid= this.getTarId(this.cityName,'city');
			this.town = this.getTownArr(cid);
			//已选省市区获取对应index
			if(this.provinceName){
				this.pChoseIndex = this.getTarIndex(this.province,this.provinceName);
			}
			if(this.cityName){
				this.cChoseIndex = this.getTarIndex(this.city,this.cityName);
			}
			if(this.townName){
				this.tChoseIndex = this.getTarIndex(this.town,this.townName);
			}
		},
		//选择省市区交互
		clickPicker(type,index,name){
			//省
			if(type === 0){
				this.pChoseIndex = index;
				this.provinceName = name;
				this.cChoseIndex = -1;
				this.tChoseIndex = -1;
				this.cityName = '';
				this.townName = '';
			}
			//市
			if(type ===1){
				this.cChoseIndex = index;
				this.cityName = name;
				this.tChoseIndex = -1;
				this.townName = '';
			}
			//区
			if(type === 2){
				this.tChoseIndex = index;
				this.townName = name;
			}
			//获取省市区数据
			this.getChosedData();
		},
		//获取已选省市区index
		getTarIndex(arr,tarName){
		    let cIndex = 0;
		    arr.map((item,index)=>{
		        if(item === tarName){
		            cIndex = index;
		        }
		    });
		    return cIndex;
		}
	},
	computed:{
		checkStatus(){
			let t = null;
			const _this = this;
			if(!_this.visible){
				_this.visible = _this._props.lotusAddressData.visible;
				//获取省市区
				_this.provinceName = _this._props.lotusAddressData.provinceName;
				_this.cityName = _this._props.lotusAddressData.cityName;
				_this.townName = _this._props.lotusAddressData.townName;
				//生成初始化数据
				_this.initFn();
				t = _this.visible;
			}
			return t;
		}
	}
}
